.board {
  /*outline: 2px solid rgb(134, 154, 189);*/
  border-style: solid;
  border-width: 2px;
  max-width: 525px;

}

.row {
  height: 35px;
}

.cell {
  width: 35px;
  height: 35px;
  outline: 1px solid rgb(134, 154, 189);
  display: inline-block;
}

.cell-green {
  background-color: green;
}

.cell-red {
  background-color: red;
}

.cell-purple {
  background-color: purple;
}
